.calendar-container {
  position: relative;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}

.calendar-background {
  width: 100%;
  height: 100%;
  object-fit: contain;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
}

.day-region {
  position: fixed;
  border: 1px dashed red;
  background: rgba(255, 0, 0, 0.3);
  color: white;
  font-weight: bold;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  pointer-events: auto;
  z-index: 10;
}

.day-region:hover {
  background: rgba(255, 255, 255, 0.3);
  outline: 2px solid yellow;
}


